<template>
  <div class="Home container has-text-centered">
    <transition name="fade">
      <div id="loading" class="loading" v-if="loading"></div>
    </transition>

    <div id="contents" class="sections__wrapper" v-show="!loading">
      <div class="header">
        <a @click="lang='jp'">日本語</a> / <a @click="lang='en'">English</a>
      </div>

      <div class="section">
        <h1 class="title">{{ contents.name }}</h1>
        <img class="icon" src="../assets/icon.png" alt="icon">
        <p class="subtitle is-5">{{ contents.affiliation }}</p>
      </div>

      <div class="section" v-for="(section, i) in contents.section" :key="i">
        <h2 class="title is-4">{{ section.title }}</h2>

        <!-- Render Section -->
        <ul>
          <li v-for="(listItem, listIdx) in section.list" :key="listIdx">
            {{ listItem }}
            <span class="article-wrapper" v-if="section.article">
              <a
                v-for="(linkItem, linkIdx) in section.article[listIdx]"
                :key="linkIdx"
                v-bind:href="linkItem[1]">{{ linkItem[0] }}
              </a>
            </span>
          </li>
        </ul>

        <!-- Render Subsection -->
        <div v-for="(subsection, j) in section.subsections" :key="j">
          <h3 class="title is-6">{{ subsection.title }}</h3>
          <ul>
            <li v-for="(listItem, k) in subsection.list" :key="k">
              {{ listItem }}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      loading: true,
      lang: 'jp',
      jp: {},
      en: {},
    };
  },
  created() {
    const request = new XMLHttpRequest();
    request.open('GET', 'https://api.github.com/repos/tan1p0p/tan1p0p.github.io/issues/1');
    request.addEventListener('load', (event) => {
      // play splash animation
      window.splashAnimation.setSpeed(0.4);
      window.splashAnimation.addEventListener('complete', () => {
        this.loading = false;
      });
      window.splashAnimation.play();

      const { body } = JSON.parse(event.target.responseText);
      const { jp, en } = JSON.parse(body);
      this.jp = jp;
      this.en = en;
    });
    request.send();
  },
  computed: {
    contents() {
      let contents = this.jp;
      if (this.lang === 'en') {
        contents = this.en;
      }
      return contents;
    },
  },
};
</script>

<style lang="scss">
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}

