  padding: 2rem 0 1rem;
  border-bottom: solid 1px rgb(200, 200, 200);
}

.loading {
  width: 100%;
  height: 100vh;
  position: absolute;
}

.icon {
  width: 4rem;
  height: auto;
}

.title {
  margin: 3rem 0 1.5rem;
